/*################################*\
    legacy  |  _c-DateBlock.less
\*################################*/
.c-DateBlock {
  font-size: 0.875rem;
  font-weight: 600;
}
.c-DateBlock > * {
  --lh: calc(0.875 * var(--line-height) * 1rem);
}
.c-DateBlock__Date {
  padding-right: 0.5rem;
  border-right: 1px solid #ccc;
  text-align: right;
}
.c-DateBlock__Desc {
  padding-left: 0.5rem;
}
.c-DateBlock__Expired {
  opacity: 0.25;
}
.c-DateBlock__MonthDay {
  font-family: 'futura-pt', 'Open Sans', 'Helvetica Neue', 'Helvetica', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  word-spacing: 1px;
  letter-spacing: 1px;
  color: var(--color-secondary, #6fc2c4);
  font-family: inherit;
}
.c-DateBlock__Link {
  text-decoration: underline;
}
.c-DateBlock {
  display: flex;
  flex-wrap: wrap;
}
.c-DateBlock__Date,
.c-DateBlock__Desc {
  margin-bottom: 0.75rem;
  margin-bottom: calc(0.5 * var(--lh));
  margin-bottom: 0.5lh;
}
@supports (margin-block-end: var(--v)) {
  .c-DateBlock__Date,
  .c-DateBlock__Desc {
    margin-bottom: unset;
    margin-block-end: calc(0.5 * var(--lh));
    margin-block-end: 0.5lh;
  }
}
.c-DateBlock__Date {
  flex: 0 0 6rem;
}
.c-DateBlock__Desc {
  flex: 0 0 calc(100% - 6rem);
}
@supports (display: grid) {
  .c-DateBlock {
    display: grid;
    grid-template-columns: 6rem auto;
    grid-row-gap: 0.75rem;
    grid-row-gap: calc(0.5 * var(--lh));
    grid-row-gap: 0.5lh;
    row-gap: calc(0.5 * var(--lh));
    row-gap: 0.5lh;
  }
  .c-DateBlock__Date,
  .c-DateBlock__Desc {
    margin-bottom: 0;
    margin-block-end: unset;
  }
}
